    );
  }
}
.button__svg {
  fill: #fff;
  @media screen and (max-width: 767px) {
    &--open {
      width: 38px;
      height: 20px;
    }
    &--close {
      width: 40px;
      height: 20px;
    }
  }
}
.open__button {
  background-color: $mainColor;
  border: none;
  cursor: pointer;

  @media screen and (min-width: $desktop) {
    display: none;
  }
}
.close__button {
  position: absolute;
  top: -50px;
  right: 15px;
  background-color: $mainColor;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  cursor: pointer;
  @media screen and (min-width: $tablet){
    top: -65px;
    right: 22px;
  }
}
.hide {
  display: none;
}

.header__social{
    @media screen and (min-width:$tablet){
        display: none;
    }
}
.hsocial__list{
    display: flex;
    align-items: center;
}
.hsocial__item{
    &:not(:last-child){
        margin-right: 40px;
    }
}
.social__svg{
    fill: $headerTextColor;
}